
@import url('https://fonts.googleapis.com/css?family=Walter+Turncoat');
@import url('https://fonts.googleapis.com/css?family=Ewert');

@import url('https://fonts.googleapis.com/css?family=Oxygen');

.button_design1{
    text-decoration: none;
    font-size: 30px!important;
    color: #FFFFFF;
    font-family: Oxygen, sans-serif;
    background : linear-gradient(to bottom, #15ff15, #433f49)!important;
    border : solid #11040B 1.9px;
    border-radius: 15px;
    padding:20px!important;
    text-shadow: 2px 1px 2px #000000;
    box-shadow: 3px 3px 5px #0D2444;
    -webkit-transition: all 0.15s ease;
    -moz-transition: all 0.15s ease;
    -o-transition: all 0.15s ease;
    transition: all 0.15s ease;
}

.button_design1:hover{
    opacity: 0.9!important;
    background: linear-gradient(to bottom, #2420ff, #1f0e0a)!important;
    border: 1.6px solid #11040B!important;
    box-shadow: 0px 1px 2px #000000!important;
}

